import React from 'react'
import { useInView } from 'react-intersection-observer'

import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

function IndexMainContent () {
    const tshld = 1

    const [ref1, inView1] = useInView({ threshold: tshld })
    const [ref2, inView2] = useInView({ threshold: tshld })
    const [ref3, inView3] = useInView({ threshold: tshld })

    const isVis1 = inView1 ? '' : 'animate__animated animate__fadeOutLeft animate__fast'
    const isVis2 = inView2 ? '' : 'animate__animated animate__fadeOut animate__fast'
    const isVis3 = inView3 ? '' : 'animate__animated animate__fadeOut animate__fast'

    return (
        <div>
            <Container fluid={true} className="index"> 
                <Row className="landing-container">
                    <Col md={false} lg={true}>
                        <h2 ref={ref1} className="animate__animated animate__fadeInLeft animate_slow"> 
                            hey! my name is
                        </h2>
                        <h1 ref={ref2} className="animate__animated animate__fadeInLeft animate__slow font-effect-neon"> 
                            davis liu
                        </h1>
                        <h4 ref={ref3} className="animate__animated animate__fadeIn animate__slow animate__delay-1s"> 
                            Welcome to My Website!
                        </h4>
                    </Col>
                </Row>

                <Row>
                    <h1 className="title">Who Am I?</h1>
                </Row>

                <Row>
                    <Col className="photo-container" >
                        <img src="https://res.cloudinary.com/davis-cloud/image/upload/v1599875160/portrait-white_q5mofi.png" alt="not found"/>
                    </Col>
                    <Col className="about-me-container" sm={12} md={8} lg={8}>
                        <p>My name is Davis Liu and I'm an aspiring and driven front-end developer. I am currently in 
                            1A Computer Engineering at the University of Waterloo in Ontario, Canada. My interests 
                            include web development and microcontrollers. 
                        </p>
                    </Col>
                </Row>
                <Row className="skills-container">
                    
                </Row>
            </Container>
        </div>
    )
}

export default IndexMainContent